header {
    background-color: #000000;
    color: #478897;
    position: fixed;
    height: 80px;
    width: 100%;
    text-align: center;
    z-index: 1;
    padding-top: 20px;
}

#score {
    font-size: 30px;
    font-family: 'Allura', cursive;
    padding-left: 200px;
}

#topScore {
    font-size: 30px;
    font-family: 'Allura', cursive;
    padding-right: 200px;
}
.jumbotron {
    width: 100%;
    text-align: center;
    background-color: #47889777;
}

h1 {
    font-size: 100px;
    font-family: 'Charm', cursive;
    color: black;
}

h3 {
    color: black;
    font-size: 30px;
    font-family: 'Charm', cursive;
    padding-top: 30px;
}
.card {
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    display: inline-block;
  }
.wrapper {
    padding-top: 50px;
    background: #00000055;
    height: 100%;
    padding: 20px;
    overflow: auto;
    max-width: 1060px;
    margin: 0px auto;
    text-align: center;
  }
